<template>
  <div>
    <v-container class="cuestionario__container">
      <title-ribbon v-bind="{ title: 'Cuestionario de Depresión' }" />
      <v-row>
        <v-col
          class="col-4 cuestionario-sideimg"
          :style="
            $vuetify.breakpoint.xs ||
              $vuetify.breakpoint.sm ||
              $vuetify.breakpoint.md
              ? 'display: none'
              : 'display: flex;'
          "
        >
          <v-img
            src="https://res.cloudinary.com/dkod5pdlc/image/upload/f_auto/terapiacl/general/cuestionario-depresion"
          />
        </v-col>
        <v-col
          :class="
            $vuetify.breakpoint.xs ||
              $vuetify.breakpoint.sm ||
              $vuetify.breakpoint.md
              ? 'col-12 cuestionario-content'
              : 'col-8 cuestionario-content'
          "
        >
          <div class="cuestionario-content-top">
            <p>
              Te invitamos a que contestes el siguiente cuestionario que puede
              orientarte si estás sufriendo algún grado de depresión. Para ello
              ante cada pregunta registra tu respuesta en el casillero
              correspondiente.
            </p>
          </div>
          <div class="cuestionario-content-main">
            <v-simple-table class="cuestionario-content-main__table">
              <template>
                <thead>
                  <tr>
                    <th
                      class="text-left"
                      colspan="2"
                    >
                      <span>
                        Durante las 2 últimas semanas, casi todos los días, la
                        mayor parte del día:
                      </span>
                    </th>
                    <th class="text-center">
                      <span>SI &nbsp; NO</span>
                    </th>
                  </tr>
                </thead>
                <tbody>
                  <tr
                    v-for="(item, i) in items"
                    :key="i"
                  >
                    <td>
                      <span class="item">
                        {{ item.Id }}
                      </span>
                    </td>
                    <td>{{ item.Texto }}</td>
                    <td
                      style="width: 100px;"
                      class="text-center"
                    >
                      <v-radio-group
                        dense
                        row
                        class="justify-center"
                      >
                        <v-radio value="radio-1" />
                        <v-radio value="radio-2" />
                      </v-radio-group>
                    </td>
                  </tr>
                </tbody>
              </template>
            </v-simple-table>
          </div>
          <div class="cuestionario-content-bottom">
            <v-btn
              class="success"
              :small="
                $vuetify.breakpoint.xs ||
                  $vuetify.breakpoint.sm ||
                  $vuetify.breakpoint.md
              "
              @click="muestraModal()"
            >
              Ver Respuesta
            </v-btn>
          </div>
        </v-col>
      </v-row>
    </v-container>
  </div>
</template>
<script>
  export default {
    name: 'CuestionarioDepresion',
    components: {
      TitleRibbon: () => import('@/components/web/base/TitleRibbon'),
    },
    data: () => {
      return {
        items: require('@/data/cuestionarioDepresion.json'),
        radio: null,
      }
    },
    methods: {
      muestraModal () {
        this.$swal({
          html: `<p style="margin-top: 30px; text-align: justify; font-family: Arial, Helvetica, sans-serif; color: #e4e4e4;">Si has respondido "Sí" a varios ítems de este cuestionario, especialmente 
          los números 1 y 2; y si las respuestas en que has marcado "Sí" te provocan un malestar
          importante y deterioro en tu vida familiar, social y/o laboral, entonces es probable
          que estés sufriendo un grado de depresión que debe ser diagnosticada y tratada por 
          especialistas del área: psicólogo y/o psiquiatra o neurólogo. Centro de Terapia del 
          Comportamiento cuenta con un gran equipo de profesionales que pueden ayudarte a superar este tipo de sufrimiento.</p>`,
          showConfirmButton: false,
          showCloseButton: true,
          showDenyButton: false,
          showCancelButton: false,
          background: '#218d5d',
          color: '#ffffff',
          width: 600,
          allowOutsideClick: false,
        })
      },
    },
    metaInfo: {
      title: 'Cuestionario de Depresión',
      titleTemplate: '%s - Centro de Terapia del Comportamiento',
      htmlAttrs: {
        lang: 'es',
        amp: true,
      },
    },
  }
</script>
